import { getInfo, setInfo, removeInfo } from "@/utils/storage"



export default {
    namespaced: true,

    // state => 提供数据
    state () {
        return {
            userInfo: getInfo()
        }
    },


    // mutations => 提供修改数据的方法
    mutations: {
    // 所有 mutations 的第一个参数都是state
        setUserInfo ( state, obj) {
            state.userInfo = obj
            setInfo(obj)
        }
    },


    // actions => 异步操作
    actions: {
        
    },

    
    // getters => 基于state数据派生的属性
    getters: {

    }
}